<template lang="pug">
.messageNotify
  .goEnd(:class="{'active': leaveEnd}" @click="handleClick")
    b-icon-chevron-down
  .newMsg(:class="{'active': isShow}" @click="handleClick") 您有新訊息
</template>

<script>
export default {
  props: {
    leaveEnd: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isShow: false,
      timer: null
    }
  },
  methods: {
    active() {
      this.isShow = true;
      this.timer = setTimeout(() => {
        this.isShow = false;
      }, 10000);
    },
    handleClick() {
      window.clearTimeout(this.timer)
      this.isShow = false;
      this.$emit('handleClick');
    }
  }
}
</script>

<style lang="scss">
.messageNotify {
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  transform: translateY(-100%);
  .goEnd {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    background-color: #ddd;
    border-radius: 50%;
    width: 35px;
    height: 35px;
    margin: 0 24px 20px auto;
    opacity: 0;
    transition: opacity 250ms ease;
    pointer-events: none;
    &.active {
      opacity: 1;
      cursor: pointer;
      pointer-events: auto;

    }
  }
  .newMsg {
    height: 0;
    font-size: 14px;
    text-align: center;
    background-color: #dddddd3b;
    cursor: pointer;
    transition: heigh 350ms ease-out;
    text-indent: -999999px;
    &.active {
      text-indent: 0;
      padding: 6px 0;
      height: 33px;
    }
  }
}
</style>